<script>
  import numeral from 'numeral';
  import moment from 'moment'
  export default {
    data() {

        return {
            userInfo: localStorage.getItem('user'),
            icon_home: "mdi-account",
            drawer:false,
            book:[],
            kitchenData:{
                lat:'',
                lng:'',
            },
            loading: false,
            selection: 1,
            icons: [
                'mdi-facebook',
                'mdi-twitter',
                'mdi-linkedin',
                'mdi-instagram',
            ],
            fab: false,
            marker: { position: { lat: 10, lng: 10 } },
            center: { lat: -27.709170290864915, lng: 30.871627324999988 },

            mapOptions: {
                disableDefaultUI: true,
                streetViewControl: false,
                mapTypeControl: true,
                zoomControl: true,
                fullscreenControl: true,
            },
        }

    },

    computed: {
        /*marker(){
            return { position: { lat: parseFloat(this.book.lat), lng: parseFloat(this.book.lng) } };
        },*/
        token(){
            return this.$route.params.token;
        },
    },
    methods: {
        formatDate(date) {
            return moment(date).format('DD-MM-YYYY HH:mm:ss');
        },
        open(url){
            let routeData = this.$router.resolve({path: url});
            var route = routeData.href.replace("pub-order/", "");
            window.open(route, '_self');

        },
        numberFormat(value){
            return numeral(value).format('0,0')
        },
        reserve () {
            this.loading = true

            setTimeout(() => (this.loading = false), 2000)
        },

        //detects location from browser
        geolocate() {
        navigator.geolocation.getCurrentPosition((position) => {
            this.marker.position = {
            lat: position.coords.latitude,
            lng: position.coords.longitude,
            };
            this.kitchenData.lat=position.coords.latitude,
            this.kitchenData.lng=position.coords.longitude,

            this.panToMarker();
        });
        },

        //sets the position of marker when dragged
        handleMarkerDrag(e) {
        this.marker.position = { lat: e.latLng.lat(), lng: e.latLng.lng() };
        },

        //Moves the map view port to marker
        panToMarker() {
        this.$refs.mapRef.panTo(this.marker.position);
        },

        //Moves the marker to click position on the map
        handleMapClick(e) {
        this.marker.position = { lat: e.latLng.lat(), lng: e.latLng.lng() };
        //console.log(e);
        },
    },
    mounted() {
        const userData = JSON.parse(this.userInfo);
        if(userData){
            this.icon_home ='mdi-home'
        }
        this.axios
            .get('pub-order-data/'+this.token)
            .then(response => (
                this.book = response.data,
                this.marker.position.lat = parseFloat(this.book.lat),
                this.marker.position.lng = parseFloat(this.book.lng),
                this.center.lat = parseFloat(this.book.lat),
                this.center.lng = parseFloat(this.book.lng)
                //this.geolocate()


            ));
        },

  }
</script>

